.radio-right-item{
        display: inline-block;
        position: relative;
        margin-left: 10px;
}
.radio-right label{
        position: relative;
}
.radio-right label::before {
        content: "\A0"; /*不换行空格*/
        display: inline-block;
        vertical-align: middle;
        font-size: 18px;
        width: 1em;
        height: 1em;
        margin-right: .4em;
        border-radius: 50%;
        border: 1px solid #ff3b44;
        text-indent: .15em;
        line-height: 1;
}
.radio-right input[type="radio"] {
        display: inline-block;
        width: 1.5em;
        height: 1.5em;
        position: absolute;
}
.radio-right input:checked + label::after {
        content: '';
        width: 10px;
        height: 6px;
        position: absolute;
        top: 4px;
        left: 4px;
        border: 2px solid #ff3b44;
        border-top: none;
        border-right: none;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
}
